.wcf--posts-slider {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 30px;

  svg {
    width: 1em;
    height: 1em;
  }

  .slide-filter {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 30px;

    li {
      cursor: pointer;
    }

    .count {
      font-size: 12px;
      vertical-align: top;
      display: inline-block;
      line-height: 1;
    }
  }

  .paginate-fill {
    display: inline-block;
    width: 100px;
    height: 3px;
    background: rgba(15, 15, 15, 0.12);
    position: relative;
    vertical-align: middle;

    &:after {
      content: '';
      width: var(--width, 100%);
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(15, 15, 15, 1);
    }
  }

  .thumb {
    line-height: 0;
  }

  .wcf-arrow {
    cursor: pointer;
    color: #666666;
    position: absolute;
    padding: 15px;
    top: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ddd;
    border-radius: 50%;
    transform: translateY(-50%);
    transition: all 0.3s;
    z-index: 9;

    &:hover {
      color: #1C1D20;
    }

    &-prev {
      left: 0;
    }

    &-next {
      right: 0;
    }
  }
}

.elementor-widget-wcf--posts-slider {
  &.filter-direction-row-reverse,
  &.filter-direction-row {
    .slide-filter {
      flex-direction: column;
    }
  }
}

.ts-navigation {
  gap: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: unset;
}

.wcf-post-meta .post-athor-area {
  display: flex;
}

.post-athor-area .author-date {
  display: flex;
  flex-direction: column;
}

.post-athor-area {
  align-items: center;
}

.wcf__slider .layout-overlay .thumb {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin-bottom: 0;
}

.wcf__slider .layout-overlay .content {
  height: 100%;
  transition: all .3s;
}

.wcf__slider .layout-overlay .content {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.swiper-slide.layout-aside.slide_item {
  display: flex;
}

.aside_thumbnail img {
  height: 100%;
  width: 100%;
}

.wcf--posts-slider {
  .layout-overlay-2 {
    .thumb {
      overflow: hidden;
      position: relative;

      .play {
        position: absolute;
        left: 50%;
        top: 50%;
      }

      img {
        width: 100%;
        height: auto;
      }
    }

    .content {
      margin: 0 auto;
      margin-top: -120px;
      position: relative;
      z-index: 1;
      text-align: center;
      background-color: #fff;
      padding: 20px;
      width: 80%;
    }

    .wcf-post-taxonomy {
      a {
        position: relative;

        &:first-child::before,
        &:last-child::after {
          position: absolute;
          content: "";
          width: 30px;
          height: 1px;
          top: 50%;
          background-color: #222;
        }

        &:first-child::before {
          left: -45px;
        }

        &:last-child::after {
          right: -45px;
        }
      }


    }

    .wcf-post-meta,
    .wcf-post-title {
      margin-top: 12px;
      margin-bottom: 12px;
    }
  }

  &.layout-gallery {

    .gallery-wrapper {
      .g-thumb {
        height: 500px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #0a0a0a;
        margin: 0 auto;


        iframe,
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    &-2 {
      position: relative;

      .gallery-wrapper * {
        height: 100%;
      }

      .thumb {
        width: 150px;
        height: 120px;
        position: relative;
        cursor: pointer;
        &:hover {
          .icon {
            opacity: 1;
          }
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        .icon {
          font-size: 50px;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          opacity: 0;
          transition: all 0.3s;
        }
      }

      .g-thumb {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .content {
        position: relative;
        z-index: 1;
        padding-top: 150px;
        padding-bottom: 350px;
        text-align: center;
        background-color: #FFFFFFD1;
      }

      .slider-wrapper.wcf__posts-slider {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;

        .swiper-slide-active {
          .icon {
            opacity: 1;
          }
        }
      }

    }
  }

}

.elementor img{
  height: 100%;
  object-fit: cover;
}